declare const document;

import * as _ from 'underscore';

import { EventEmitterExtended } from './EventEmitterExtended';

import { Utils } from './utils';
import { HtmlHelperResponsiveModule } from './htmlHelperResponsiveModule';

const defaults = {
    processOnLoad: false,
    processOnResize: false,
    loadingImageUrl: '',
    errorImageUrl: '',
    attributes: {
        loadingImage: 'data-loading-image',
        errorImage: 'data-error-image',
        dpi: 'data-dpi',
        imageSource: 'data-src',
        fileSource: 'data-href',
        enableOffline: 'data-offline',
        enableResponsive: 'data-responsive'
    },
    responsiveParams: {
        //http://docs.telerik.com/platform/backend-services/javascript/responsive-images/responsive-images-parameters
    }
};

export interface HtmlHelperConfig {
    appId: string;
    scheme?: string;
    processOnLoad?: boolean;
    processOnResize?: boolean;
    loadingImageUrl?: string;
    errorImageUrl?: string;
    attributes?: {
        loadingImage?: string;
        errorImage?: string;
        dpi?: string;
        imageSource?: string;
        enableResponsive?: string;
    };
    responsiveParams?: {};
}

export interface HtmlHelperProcessSettings {
    responsive?: boolean;
}

export class HtmlHelper extends EventEmitterExtended {
    settings = {
        urlTemplate: '[protocol][hostname][appid]/[operations][url]',
        server: 'localhost:7071/1/'
    };

    options: HtmlHelperConfig;
    responsive: HtmlHelperResponsiveModule;

    constructor(config: HtmlHelperConfig) {
        super();

        this.options = _.extend({}, defaults, config);
        this.options.attributes = _.extend(
            {},
            defaults.attributes,
            config.attributes
        );
        this.options.responsiveParams = _.extend(
            {},
            defaults.responsiveParams,
            config.responsiveParams
        );

        this.responsive = new HtmlHelperResponsiveModule(this);

        this._init();
    }

    _init() {
        var self = this;
        if (self.options.processOnLoad) {
            window.addEventListener('load', this.processAll.bind(this), false);
        }

        if (this.options.processOnResize) {
            window.addEventListener(
                'resize',
                _.debounce(this.processAll.bind(this), 300),
                false
            );
        }
    }

    _triggerOnProcessed(args) {
        this.emit('processed', args);
    }

    _defaultProcessSettings(settings): HtmlHelperProcessSettings {
        return _.defaults({}, settings, {
            responsive: true
        });
    }

    _setLoadingUrl(element): Promise<any> {
        var loadingImageUri =
            element.getAttribute(this.options.attributes.loadingImage) ||
            this.options.loadingImageUrl;
        if (!loadingImageUri || Utils.isElement.anchor(element)) {
            return Promise.resolve();
        }

        return this._setUrl(element, loadingImageUri, true);
    }

    _getBackgroundSrc(el) {
        var elStyle = window.getComputedStyle(el, null);
        var backgrImage = elStyle.getPropertyValue('background-image');

        var img: any = backgrImage !== 'none' ? backgrImage : false;
        if (img) {
            img = img.replace(/url\(('?"?)(.*?)\1\)/gi, '$2');
        }

        return img;
    }

    _setErrorUrl(element) {
        var errorImageUrl =
            element.getAttribute(this.options.attributes.errorImage) ||
            this.options.errorImageUrl;
        if (!errorImageUrl || Utils.isElement.anchor(element)) {
            return Promise.resolve();
        }

        return this._setUrl(element, errorImageUrl, true) as Promise<any>;
    }

    _setUrl(element, url, apply: boolean = false) {
        var self = this;
        return new Promise(function(resolve, reject) {
            var elAttr = self._getAttr(element);
            if (
                Utils.isElement.image(element) &&
                elAttr === self.options.attributes.imageSource
            ) {
                if (apply) {
                    element.src = url;
                    element.style.visibility = 'visible';
                } else {
                    var img = new Image();

                    img.onerror = function() {
                        img = null;
                        reject(new Error(`Can\'t be loaded: ${url}`));
                    };

                    img.onload = function() {
                        img = null;
                        self
                            ._setUrl(element, url, true)
                            .then(resolve)
                            .catch(reject);
                    };

                    img.src = url;
                }
            } else {
                apply = true;
                if (elAttr) {
                    var attr;
                    if (elAttr === self.options.attributes.imageSource) {
                        attr = 'src';
                    } else {
                        attr = _.last(elAttr.split('-'));
                    }

                    element.setAttribute(attr, url);
                } else {
                    element.style.backgroundImage = 'url(' + url + ')';
                }
            }

            if (apply) {
                resolve();
            }
        });
    }

    _getAttr(element) {
        if (element.getAttribute(this.options.attributes.imageSource)) {
            return this.options.attributes.imageSource;
        }
    }

    _getUrl(element) {
        var url =
            element.getAttribute(this.options.attributes.imageSource) ||
            this._getBackgroundSrc(element);

        return url;
    }

    _wrapElements(elements) {
        var self = this;

        var results = _.map(elements, function(element: any) {
            var tag = element.tagName.toLowerCase();

            var evaluateDataAttr = function evaluateDataAttr(attr) {
                // data-a - true
                // data-a="" - true
                // data-a="true" - true
                // data-a="anything" - true
                // data-a="false" - false
                // missing - false
                var val;
                var dataVal = (element.attributes[attr] || { value: null })
                    .value;
                if (dataVal === '') {
                    val = true;
                } else if (!dataVal) {
                    val = false;
                } else {
                    try {
                        val = JSON.parse(dataVal);
                    } catch (e) {
                        val = true;
                    }
                }

                return val;
            };

            var canResponsive = evaluateDataAttr(
                self.options.attributes.enableResponsive
            );

            return {
                item: element,
                tag: tag,
                operations: {
                    responsive: canResponsive
                }
            };
        });

        return results;
    }

    /**
     * @method process
     * @memberOf Helpers.html
     * @param {HtmlElement|HtmlElement[]} elements
     * @param {Object} settings A settings specifying custom behavior.
     * @param {boolean} [settings.responsive] Whether to process the data-responsive attributes that help implement Responsive Images.
     * @param {boolean} [settings.offline] Whether to process the data-offline attributes that help implement offline files.
     * @param {Function} [success] A success callback.
     * @param {Function} [error] An error callback.
     */

    /**
     * @method process
     * @memberOf Helpers.html
     * @param {HtmlElement|HtmlElement[]} elements
     * @param {Object} settings A settings specifying custom behavior.
     * @param {boolean} [settings.responsive] Whether to process the data-responsive attributes that help implement Responsive Images.
     * @param {boolean} [settings.offline] Whether to process the data-offline attributes that help implement offline files.
     * @returns {Promise} A promise to the process state.
     */
    process(
        elements: Array<HTMLElement>,
        settings?: HtmlHelperProcessSettings,
        success?: Function,
        error?: Function
    ): Promise<any> {
        return Utils.buildPromise(
            resolve => {
                settings = this._defaultProcessSettings(settings);
                if (
                    _.isArray(elements) ||
                    (elements as any) instanceof NodeList ||
                    (elements as Array<any>).length
                ) {
                    elements = _.flatten(elements);
                } else {
                    elements = [<any>elements];
                }

                var wrappedElements = this._wrapElements(elements);
                var promises = [];
                _.each(wrappedElements, element => {
                    var result = {
                        element: element.item,
                        responsive: false,
                        offline: false
                    };

                    var dataUrl = this._getUrl(result.element);

                    if (!dataUrl) {
                        return promises.push(Promise.resolve(result));
                    }

                    var canResponsive = settings.responsive
                        ? element.operations.responsive
                        : false;

                    if (!canResponsive) {
                        return promises.push(
                            this._setUrl(
                                result.element,
                                dataUrl,
                                true
                            ).then(function() {
                                return result;
                            })
                        );
                    }

                    var promise = this._setLoadingUrl(result.element);
                    var handleOperation = function handleOperation(
                        operation,
                        url
                    ) {
                        if (url) {
                            result[operation] = true;
                            return url;
                        }
                    };

                    if (canResponsive) {
                        promise = promise.then(() => {
                            return this.responsive
                                .responsiveImage(element, dataUrl)
                                .then(handleOperation.bind(this, 'responsive'));
                        });
                    }

                    promise = (<Promise<any>>promise)
                        .then(finalUrl => {
                            return this._setUrl(result.element, finalUrl).then(
                                () => result
                            );
                        })
                        .catch(err => {
                            return this._setErrorUrl(
                                result.element
                            ).then(() => {
                                throw {
                                    element: result.element,
                                    error: err
                                };
                            });
                        });

                    promises.push(promise);
                });

                Utils.promisesAllSettled(promises).then(results => {
                    var processed = [];
                    var failed = [];

                    _.each<any>(results, function(result) {
                        if (result.state === 'fulfilled') {
                            processed.push(result.value);
                        } else {
                            failed.push(result.reason);
                        }
                    });

                    var result = {
                        processed: processed,
                        failed: failed
                    };

                    this._triggerOnProcessed(result);
                    resolve(result);
                });
            },
            success,
            error
        );
    }

    /**
     * @method processAll
     * @memberOf Helpers.html
     * @param {Object} settings A settings specifying custom behavior.
     * @param {boolean} [settings.responsive] Whether to process the data-responsive attributes that help implement Responsive Images.
     * @param {boolean} [settings.offline] Whether to process the data-offline attributes that help implement offline files.
     * @param {Function} [success] A success callback.
     * @param {Function} [error] An error callback.
     */

    /**
     * @method processAll
     * @memberOf Helpers.html
     * @param {Object} settings A settings specifying custom behavior.
     * @param {boolean} [settings.responsive] Whether to process the data-responsive attributes that help implement Responsive Images.
     * @param {boolean} [settings.offline] Whether to process the data-offline attributes that help implement offline files.
     * @returns {Promise} A promise to the process state.
     */
    processAll(settings, success?: Function, error?: Function) {
        settings = this._defaultProcessSettings(settings);
        var responsiveSelector =
            '[' + this.options.attributes.enableResponsive + ']';

        var responsiveElements = [];
        if (settings.responsive) {
            responsiveElements = document.querySelectorAll(responsiveSelector);
        }

        var slice = [].slice;
        var elements = _.unique(slice.call(responsiveElements).concat([]));

        return this.process(
            elements as Array<HTMLElement>,
            settings,
            success,
            error
        );
    }
}

module.exports = HtmlHelper;



