import * as _ from 'underscore';

export class Utils {
    static _stringCompare = function(string, check) {
        return string.toLowerCase() === check;
    };

    static isElement = {
        _isElement: function(el, check) {
            var tag = el;
            if (typeof tag !== 'string') {
                if (el instanceof HTMLElement) {
                    tag = el.tagName;
                }
            }
            return Utils._stringCompare(tag, check);
        },
        image: function(el) {
            return Utils.isElement._isElement(el, 'img');
        },
        anchor: function(el) {
            return Utils.isElement._isElement(el, 'a');
        }
    };

    static getCallbacks<T>(
        success?,
        error?
    ): { promise: Promise<T>; success: T; error: Error } {
        var promise;
        var createPromise = function(): Promise<T> {
            return new Promise<T>(function(resolve, reject) {
                success = (data: T) => {
                    resolve(data);
                };
                error = (error: Error) => {
                    reject(error);
                };
            });
        };

        if (typeof success !== 'function' && typeof error !== 'function') {
            promise = createPromise();
        }

        return { promise: promise, success: success, error: error };
    }

    static buildPromise<T>(operation, success, error): Promise<T> {
        var callbacks = Utils.getCallbacks<T>(success, error);
        operation(callbacks.success, callbacks.error);
        return <Promise<T>>callbacks.promise;
    }

    static promisesAllSettled = function(promises) {
        return Utils._mapPromises(promises);
    };
    static _mapPromises = function(promises, stopOnError = false) {
        var promiseCount = _.size(promises);
        var resultObj = Array.isArray(promises) ? [] : {};
        if (promiseCount === 0) {
            return Promise.resolve(resultObj);
        }
        return new Promise(function(resolve, reject) {
            var aborted = false;
            var settledCount = 0;
            var storeResponse = function(response, key, isError = false) {
                if (aborted) return;
                var settlement;
                if (isError) {
                    settlement = { state: 'rejected', reason: response };
                } else {
                    settlement = { state: 'fulfilled', value: response };
                }
                resultObj[key] = settlement;
                settledCount++;
                if (settledCount === promiseCount) {
                    resolve(resultObj);
                }
            };
            _.each(promises, function(promise, key) {
                (promise as Promise<any>).then(
                    function(res) {
                        storeResponse(res, key);
                        return Promise.resolve(res);
                    },
                    function(err) {
                        if (stopOnError) {
                            reject(err);
                            aborted = true;
                        }
                        storeResponse(err, key, true);
                        return Promise.reject(err);
                    }
                );
            });
        });
    };
}



